					icon: 'error'
				});
				this.loading_yn='N';
				return;
			}
			var same_yn='Y';
			for(var i=0;i<this.selected_items3.length;i++){
				for(var j=0;j<this.selected_items3.length;j++){
					if(this.selected_items3[i].clm_material_id != this.selected_items3[j].clm_material_id){
						same_yn='N';
					}	
				}
				for(var j=0;j<this.AfterCountItems.length;j++){
					if(this.selected_items3[i].clm_material_id != this.AfterCountItems[j].clm_material_id){
						same_yn='N';
					}	
				}
			}
			if(same_yn=='N'){
				alert('규격이 다른 자재가 선택되었습니다.');
				this.loading_yn='N';
				return;
			}
			this.same_id_yn=='N';
			for(var i=0;i<this.selected_items3.length;i++){
				this.AfterCountItems.push({
					clm_after_joborder_id:'',
					clm_seq:'',
