		WorkEndComplete(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.m_product_poor_count==''){
				this.m_product_poor_count=0;
			}
			if(Number(this.m_product_produce_count) > Number(this.m_product_usable_count)){
				alert('작업 완료 수량이 작업 미완료 수량을 초과 하였습니다.');
				this.loading_yn='N';
			}else if(Number(this.m_product_produce_count)<=0){
				alert('작업 완료 수량이 0또는 음수입니다.');
				this.loading_yn='N';
			}else if(Number(this.m_product_produce_count)% 1!= 0){
				alert('작업 완료 수량이 정상적이지 않습니다.');
				this.loading_yn='N';
			}else if(this.m_product_produce_count==''){
				alert('작업 완료 수량이 정상적이지 않습니다.');
				this.loading_yn='N';
			}else if(Number(this.m_product_poor_count)% 1!= 0  || Number(this.m_product_poor_count)<0){
				alert('불량 수량이 정상적이지 않습니다.');
				this.loading_yn='N';
			}else if(Number(this.m_product_poor_count) > Number(this.m_product_usable_count)){
				alert('불량 수량이 작업 미완료 수량을 초과 하였습니다.');
				this.loading_yn='N';
			}else if(Number(this.m_product_poor_count)+Number(this.m_product_produce_count) > Number(this.m_product_usable_count)){
				alert('작업 완료 수량과 불량 수량의 합이 작업 미완료 수량을 초과 하였습니다.');
				this.loading_yn='N';
			}else if(this.m_joborder_user=='' || this.m_joborder_user_name==''){
				alert('작업자를 선택해주시기 바랍니다.');
				this.loading_yn='N';
			}else{
				// if(Number( this.m_product_produce_count )+Number(this.m_product_poor_count)== Number(this.m_product_usable_count)){
				// 	if(confirm('후공정 최종 완료 시 LotNo가 바뀔 수 있습니다.\n진행하시겠습니까?')){
						
				// 	}else{
				// 		this.loading_yn='N';
				// 		return;
				// 	}
				// }
				axios.get('/back/MOB001005/SelectCompleteCount',{
				params: {
					clm_after_joborder_id: this.m_after_joborder_id,
					clm_main_after_joborder_id: this.m_main_after_joborder_id,
					clm_process_option_id: this.selected_items[0].clm_process_option_id,
					clm_process_seq: this.m_process_seq
					}
				}).then(
				response => {
					if(Number(this.m_product_produce_count)+Number(this.m_product_poor_count)<=Number(String(response.data).replace(/,/gi, ""))){
						axios.post('/back/MOB001005/CompleteAfterProcess', null, { params: {
							clm_main_after_joborder_id: this.m_main_after_joborder_id,
							clm_after_joborder_id:this.m_after_joborder_id,
							clm_process_option_id: this.m_original_process_option_id,
							clm_product_id : this.m_product_id,
							clm_client_order_id: this.m_client_order_id,
							clm_joborder_id:this.m_joborder_id,
							clm_material_order_id: this.m_material_order_id,
							clm_order_income_seq:this.m_order_income_seq,
							clm_income_material_seq: this.m_income_material_seq,
							clm_material_leftover_seq:this.m_material_leftover_seq,
							clm_m_p_gubun: this.m_gubun,
							clm_process_seq:this.m_process_seq,
							clm_product_count:Number(this.m_product_produce_count),
							clm_poor_count:Number(this.m_product_poor_count),
							clm_usable_count:Number(this.m_product_usable_count),
							clm_joborder_user : this.m_joborder_user,
							user_id: this.user_id,
							clm_user:this.$session.get('user_id')
						}})
						.then(response => {
							if (response.status == '200') {
								if(response.data=='N'){
									this.loading_yn='N';
									Swal.fire({
										title: '정상적으로 저장 되었습니다.',
										icon: 'success'
									});
									this.getList();
									this.WorkEndModal=false;
									this.selected_items=[];
								}else{
									this.loading_yn='N';
									alert('진행중인 공정이 있습니다.');
									this.getList();
									this.WorkEndModal=false;
									this.selected_items=[];
								}
							}
						});
					}else{
						alert('이미 완료된 작업입니다.');
						this.getList();
						this.WorkEndModal=false;
						this.selected_items=[];
						this.loading_yn='N';
						return;
					}
				});
				
				
			}
		},
